export default {
	data() {
		return {
			List0:[],//园区内容
			List:[],
			List1:[], 
			List2:[],
			com_List:[],
			List0Len:0,
			ListLen:0,
			ListLen1:0,
			List2Len:0,
			com_List_Len:0,
		}
	},
	onLoad:function(){
		var _self = this
		// _self.getInfo0() 
		_self.getInfo()
		_self.getInfo1()
		_self.getInfo2()
	},
	onShow:function(){
		var _self = this
		_self.getInfo()
		_self.getInfo1()
		_self.getInfo2()
	},
	methods: {
		
		
		/**
		 * 园区内容收藏
		 */
		getInfo0:function(){
			var _self = this
			 uni.request({
			 	url:_self.$api+"dockingManager/releaseHideQuery",
				data:{id:0,optionId:uni.getStorageSync("openId")},
				method:"GET",
				success:function(res){
					console.log(res)
					_self.List0Len = res.data.length
					_self.List0 = res.data
					//.slice(0,2)  
				}
			 })
		},
		/**
		 * 我的供应收藏
		 */
		getInfo:function(){
			var _self = this
			 uni.request({
			 	url:_self.$api+"dockingManager/demandHideQuery",
				data:{id:0,optionId:uni.getStorageSync("openId")},
				method:"GET", 
				success:function(res){
					console.log(res)
					_self.ListLen = res.data.length
					_self.List = res.data
					console.log(_self.List.length)
					//.slice(0,2)  
				}
			 })
		},
		/**
		 * 园区服务收藏
		 */
		getInfo1:function(){
			var _self = this
			 uni.request({
			 	url:_self.$api+"dockingManager/totalHideQuery",
				data:{id:0,optionId:uni.getStorageSync("openId")},
				method:"GET",
				success:function(res){
					console.log(res)
					_self.List1 = res.data
					console.log(res.data)
					_self.ListLen1 = _self.List1.length
				}
			 })
		},
		/**
		 * 我的活动收藏
		 */
			getInfo2:function(){
			var _self = this 
			 uni.request({
			 	url:_self.$api+"dockingManager/hideQuery",
				data:{id:0,optionId:uni.getStorageSync("openId")},
				method:"GET",
				success:function(res){
					_self.List2 = res.data
					_self.List2Len = _self.List2.length
				}
			 })
		},
		/**
		 * @param {Object} id
		 * @param {Object} mark
		 * @param {Object} trade
		 * @param {Object} branch
		 * 获取入住企业
		 */
		// getComList:function(){
		// 	var _self = this 
		// 	 uni.request({
		// 	 	url:_self.$api+"dockingManager/hideQuery",
		// 		data:{id:0,optionId:uni.getStorageSync("openId")},
		// 		method:"GET",
		// 		success:function(res){
		// 			_self.com_List = res.data
		// 			_self.com_List_Len = _self.com_List.length
		// 		}
		// 	 })
		// 	
		// },
		/**
		 * 企业收藏
		 * 
		 */
		/**
		 * 供应详情
		 */
		goInfoDetail:function(id,mark,branch){
			var _self = this
			console.log(id)
			console.log(mark)
			if(branch ==0){
					uni.navigateTo({
						url:"../../infoPage/infoDetail/infoDetail?id="+id+'&mark='+mark
					})
				
			}else{
				uni.navigateTo({
				url:"../../pageChildren/productDetail/productDetail?id="+id+'&mark='+mark+'&branch='+branch
				})
			}
			
		},
		/**
		 * 活动详情
		 */
		goActiveDetail:function(id){
			console.log('1231')
			var _self = this
			uni.navigateTo({
				url:"../../pageChildren/activity/activity?id="+id
			})
		},
		/**
		 * 其他收藏详情
		 */
		goLawerDetail:function(id,mark){
			var _self = this
			uni.navigateTo({
				url:"../../serverPage/taxDetail/taxDetail?id="+id+'&pull='+mark
			})
		}
	},
	filters:{
		getPic:function(res){
			if(res == "" || res==undefined){ 
				return " "
			}else{
				return res.split(',')[0] 
			}
		},
	}
}
